@-webkit-keyframes ecg {
  0% {
    left: 0;

  }

  25% {
    left: 25%;
    -webkit-transform: translate(0,0);
            transform: translate(0,0);

  }

  50% {
    left: 50%;
    -webkit-transform: translate(0,0);
            transform: translate(0,0);

  }

  26%,51%,76% {
    -webkit-transform: translate(0,-30px);
            transform: translate(0,-30px);

  }

  27%,52%,77% {
    -webkit-transform: translate(0,0);
            transform: translate(0,0);

  }

  28%,53%,78% {
    -webkit-transform: translate(0,30px);
            transform: translate(0,30px);

  }

  29%,54%,79% {
    -webkit-transform: translate(0,0);
            transform: translate(0,0);

  }

  30%,55%,80% {
    -webkit-transform: translate(0,10px);
            transform: translate(0,10px);

  }

  31%,56%,81% {
    -webkit-transform: translate(0,0);
            transform: translate(0,0);

  }

  75% {
    left: 75%;
    -webkit-transform:translate(0,0);
            transform:translate(0,0);

  }

  100% {
    left: 100%;

  }
}

